import React,{useState,useEffect,useContext} from 'react'
import {UserContext} from '../../App'
import {Link, useHistory} from 'react-router-dom'

const Admin  = ()=>{
    const history = useHistory()

    const [data,setData] = useState([])
    const [data2,setData2] = useState([])
    const [choice,setChoice] = useState("unaccredited");
    const {state,dispatch} = useContext(UserContext)
    useEffect(()=>{
       fetch('/unaccredited',{
           headers:{
               "Authorization":"Bearer "+localStorage.getItem("jwt")
           }
       }).then(res=>res.json())
       .then(result=>{
           console.log(result.groups)
           if(result)
           setData( result.groups )
       })   
       fetch('/accredited',{
        headers:{
            "Authorization":"Bearer "+localStorage.getItem("jwt")
        }
     }).then(res=>res.json())
    .then(result=>{
        console.log(result.groups)
        if(result)
        setData2(result.groups)
    })
    },[choice])

    const join = (id)=>{
        fetch('/join',{
            method:"put",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            },
            body:JSON.stringify({
                groupId:id
            })
        }).then(res=>res.json())
        .then(result=>{
            console.log(result)
            //history.push('/group/'+id)
          })
        .catch(err=>{
            console.log(err)
        })
    }

    const leave = (id)=>{
        fetch('/leave',{
            method:"put",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            },
            body:JSON.stringify({
                groupId:id
            })
        }).then(res=>res.json())
        .then(result=>{
            console.log(result)
            //history.push('/group/'+id)
          })
        .catch(err=>{
            console.log(err)
        })
    }
    const viewMore = (id)=>{
        history.push('groupProfile?id='+id);
    }
    const accredit = (id)=>{
        fetch('/accredit?id='+id,{
            headers:{
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            }
        }).then(res=>res.json())
        .then(result=>{
            console.log(result)
            setChoice("accredited")
        })
    }

    const unaccredit = (id)=>{
        fetch('/unaccredit?id='+id,{
            headers:{
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            }
        }).then(res=>res.json())
        .then(result=>{
            console.log(result.group)
            setChoice("unaccredited")
        })
    }
  const unlikePost = (id)=>{
        fetch('/dislike',{
            method:"put",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            },
            body:JSON.stringify({
                postId:id
            })
        }).then(res=>res.json())
        .then(result=>{
          //   console.log(result)
          const newData = data.map(item=>{
              if(item._id==result._id){
                  return result
              }else{
                  return item
              }
          })
          setData(newData)
        }).catch(err=>{
          console.log(err)
      })
  }

   return ( 
       <div className="home">
           <center><h3><p>Admin Log</p></h3></center>
               
    
           <center><h4><p>Accreditation Requests</p></h4></center>

      <table class="striped">
      <thead>
        <tr>
            <th>Group ID</th>
            <th>Group Name</th>
            <th>Domain</th>
            <th>Description</th>
            <th>Status</th>
            <th>Action</th>
        </tr>
      </thead>

      <tbody>
        
          

            {
               data.map(item=>{
                   return(
                    <tr>
                    <td>{item._id}</td>
                    <td>{item.name}</td>
                    <td>{item.domain}</td>
                    <td>{item.description}</td>
                    <td>{item.status}</td>
                    <td><button onClick={()=>{accredit(item._id)}} >Accredit</button></td>
                    </tr>
                   )
               })
           }
              
      </tbody>
    </table>

    <center><h4><p>Accredited Study Groups</p></h4></center>

    <table class="striped">
      <thead>
        <tr>
            <th>Group ID</th>
            <th>Group Name</th>
            <th>Domain</th>
            <th>Description</th>
            <th>Status</th>
            <th>Action</th>
        </tr>
      </thead>

      <tbody>
        
          

            {
               data2.map(item=>{
                   return(
                    <tr>
                    <td>{item._id}</td>
                    <td>{item.name}</td>
                    <td>{item.domain}</td>
                    <td>{item.description}</td>
                    <td>{item.status}</td>
                    <td><button onClick={()=>{unaccredit(item._id)}} >Unaccredit</button></td>
                    </tr>
                   )
               })
           }
              
      </tbody>
    </table>
       </div>
   )
}


export default Admin