import React, {useState, useContext, useEffect} from 'react'
import {useHistory} from 'react-router-dom'
import M from 'materialize-css'
import {UserContext} from '../../App'
import { useQRCode } from 'react-hook-qrcode';

const GroupCreation = () =>{
    const {state, dispatch} = useContext(UserContext)
    const history = useHistory()
    const [name, setName] = useState("")
    const [description, setDescription] = useState("")
    const [domain, setDomain] = useState("")
    const [motivation, setMotivation] = useState("")
    const [reasoning, setReasoning] = useState("")
    const [states, setStates] = useState("")
    const [country, setCountry] = useState("")
    const [image, setImage] = useState("")
    const [url, setUrl] = useState("")

    useEffect(()=>{
        if(url)
        {
            fetch("/creategroup",{
                method:"post",
                headers:{
                    "Content-Type":"application/json",
                    "Authorization":"Bearer "+localStorage.getItem("jwt")
                },
                body: JSON.stringify({
                    name,
                    description,
                    domain,
                    motivation,
                    reasoning,
                    states,
                    country,
                    pic:url
                })
            }).then(res=> res.json())
            .then(data=>{
                console.log(data);
               if(data.error){
                  M.toast({html: data.error,classes:"#c62828 red darken-3"})
               }
               else{
                   M.toast({html:"Request for Group Creation Successful",classes:"#43a047 green darken-1"})
                   history.push('/')
               }
            }).catch(err=>{
                console.log(err)
            })
        }
    }, [url])
    
    const [inputRef] = useQRCode({
        text: !name ? "0" : name ,
        options: {  
          level: 'M',
          margin: 7,
          scale: 1,
          width: 200,
          color: {
            dark: '#010599FF',
            light: '#FFBF60FF',
          },
        },
      });

    const PostData=()=>{
        const data = new FormData()
        data.append("file",image)
        data.append("upload_preset","chordsNstrings")
        data.append("cloud_name", "chordsnstrings")
        fetch("https://api.cloudinary.com/v1_1/chordsnstrings/image/upload",{
            method:"post",
            body:data
        })
        .then(res=>res.json())
        .then(data=>{
            setUrl(data.url)
            console.log(data.url)
        })
        .catch(err=>{
            console.log(err)
        })
    }

    return(
        <div className="mycard">
             <div className="card auth-card input-field  #e8eaf6 indigo lighten-5">
                <h4>Create Group</h4>
                <input type="text" 
            placeholder="Group Name"
            value = {name}
            onChange= {(e)=>setName(e.target.value)}/>

            <input type="text" 
            placeholder="Description"
            value={description}
            onChange={(e)=> setDescription(e.target.value)}/>

            <input type="text" 
            placeholder="Domain"
            value={domain}
            onChange={(e)=> setDomain(e.target.value)}/>

            <input type="text" 
            placeholder="Motivation for Group Creation"
            value={motivation}
            onChange={(e)=> setMotivation(e.target.value)}/>

            <input type="text" 
            placeholder="Why the group should be accredited?"
            value={reasoning}
            onChange={(e)=> setReasoning(e.target.value)}/>

            <input type="text" 
            placeholder="State"
            value={states}
            onChange={(e)=> setStates(e.target.value)}/> 

            <input type="text" 
            placeholder="Country"
            value={country}
            onChange={(e)=> setCountry(e.target.value)}/> 

            <div className="file-field input-field">
                <div className="btn #64b5f6 blue darken-1">
                    <span> Upload Image</span>
                    <input type="file"
                    onChange={(e)=>setImage(e.target.files[0])}/>
                </div>
                <div className="file-path-wrapper">
                    <input className="file-path validate" type="text"/>
                </div>
            </div>

              
                <button className="btn waves-effect waves-light #64b5f6 blue darken-1"
                onClick={()=> PostData()}>
                    Create
                </button>


                
            </div>
            <div className="card auth-card input-field #e8eaf6 indigo lighten-5">
                {name}
                <br />
                <canvas ref={inputRef} /> 
            </div>
        </div>
    )
}

export default GroupCreation