import React from 'react';
import axios from 'axios';

function Payment() {
  function loadScript(src) {
    return new Promise((resolve) => {
      const script = document.createElement('script');
      script.src = src;
      script.onload = () => {
        resolve(true);
      };
      script.onerror = () => {
        resolve(false);
      };
      document.body.appendChild(script);
    });
  }

  async function displayRazorpay() {
    const res = await loadScript(
      'https://checkout.razorpay.com/v1/checkout.js'
    );

    if (!res) {
      alert('Razorpay SDK failed to load. Are you online?');
      return;
    }

    const result = await axios.post('http://localhost:5000/payment/orders');

    if (!result) {
      alert('Server error. Are you online?');
      return;
    }

    const { amount, id: order_id, currency } = result.data;

    const options = {
      key: 'rzp_test_KNoCu64wQKXO55', // Enter the Key ID generated from the Dashboard
      amount: amount.toString(),
      currency: currency,
      name: 'Study Chain.',
      description: 'Test Transaction',
      //image: { logo },
      order_id: order_id,
      handler: async function (response) {
        const data = {
          orderCreationId: order_id,
          razorpayPaymentId: response.razorpay_payment_id,
          razorpayOrderId: response.razorpay_order_id,
          razorpaySignature: response.razorpay_signature,
        };

        const result = await axios.post('http://localhost:5000/payment/success', data);

        alert(result.data.msg);
      },
      prefill: {
        name: '<YOUR NAME>',
        email: 'example@example.com',
        contact: '9999999999',
      },
      notes: {
        address: 'Example Corporate Office',
      },
      theme: {
        color: '#61dafb',
      },
    };

    const paymentObject = new window.Razorpay(options);
    paymentObject.open();
  }

  return (
    // <div className='App'>
    //     <p>Become a Premium Study Chain member now!</p>
    //     <button className='App-link' onClick={displayRazorpay}>
    //       Pay 500
    //     </button>
    // </div>



<div className="mycard">
<div className="card auth-card input-field">
        <h2> StudyChain </h2>
        <h6>Become a Premium Study Chain member now!</h6>
        <h6> Get exclusive access to our paid services</h6>
        <br/>
        <button className="btn waves-effect waves-light #64b5f6 blue darken-1" onClick={displayRazorpay}>
          Pay 500
        </button>

</div>
</div>

  );
}

export default Payment;