
import React, {useEffect, useState, useContext} from 'react'
import {UserContext} from '../../App'
import queryString from 'query-string';
import { Link, useLocation, useHistory } from 'react-router-dom';

const GroupProfile = () =>{
    const [grp, setGrp] = useState([])
    const [image, setImage] = useState("")
    //const [url, setUrl] = useState("")
    const {state, dispatch} = useContext(UserContext)
    const location = useLocation();
    const history = useHistory()
    const [data,setData] = useState([])
    useEffect(()=>{
       const { id } = queryString.parse(location.search);
       console.log(id);
       fetch('/groupByPost?id='+id,{
           headers:{
               "Authorization":"Bearer "+localStorage.getItem("jwt")
           }
       }).then(res=>res.json())
       .then(result=>{
           console.log(result)
           setData(result.posts)
       })
    },[])

    const join = (id)=>{
        fetch('/join',{
            method:"put",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            },
            body:JSON.stringify({
                groupId:id
            })
        }).then(res=>res.json())
        .then(result=>{
            console.log(result)
            //history.push('/group/'+id)
          })
        .catch(err=>{
            console.log(err)
        })
    }

    const leave = (id)=>{
        fetch('/leave',{
            method:"put",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            },
            body:JSON.stringify({
                groupId:id
            })
        }).then(res=>res.json())
        .then(result=>{
            console.log(result)
            //history.push('/group/'+id)
          })
        .catch(err=>{
            console.log(err)
        })
    }
    
    const createPost = ()=>{
        const { id } = queryString.parse(location.search);
        history.push('/create?id='+id);
    }
    const likePost = (id)=>{
          fetch('/like',{
              method:"put",
              headers:{
                  "Content-Type":"application/json",
                  "Authorization":"Bearer "+localStorage.getItem("jwt")
              },
              body:JSON.stringify({
                  postId:id
              })
          }).then(res=>res.json())
          .then(result=>{
                   //   console.log(result)
            const newData = data.map(item=>{
                if(item._id==result._id){
                    return result
                }else{
                    return item
                }
            })
            setData(newData)
          }).catch(err=>{
              console.log(err)
          })
    }
    const unlikePost = (id)=>{
          fetch('/dislike',{
              method:"put",
              headers:{
                  "Content-Type":"application/json",
                  "Authorization":"Bearer "+localStorage.getItem("jwt")
              },
              body:JSON.stringify({
                  postId:id
              })
          }).then(res=>res.json())
          .then(result=>{
            //   console.log(result)
            const newData = data.map(item=>{
                if(item._id==result._id){
                    return result
                }else{
                    return item
                }
            })
            setData(newData)
          }).catch(err=>{
            console.log(err)
        })
    }

    const makeComment = (text,postId)=>{
          fetch('/comment',{
              method:"put",
              headers:{
                  "Content-Type":"application/json",
                  "Authorization":"Bearer "+localStorage.getItem("jwt")
              },
              body:JSON.stringify({
                  postId,
                  text
              })
          }).then(res=>res.json())
          .then(result=>{
              console.log(result)
              const newData = data.map(item=>{
                if(item._id==result._id){
                    return result
                }else{
                    return item
                }
             })
            setData(newData)
          }).catch(err=>{
              console.log(err)
          })
    }

    const deletePost = (postid)=>{
        fetch(`/deletepost/${postid}`,{
            method:"delete",
            headers:{
                Authorization:"Bearer "+localStorage.getItem("jwt")
            }
        }).then(res=>res.json())
        .then(result=>{
            console.log(result)
            const newData = data.filter(item=>{
                return item._id !== result._id
            })
            setData(newData)
        })
    }
    useEffect(()=>{
        const { id } = queryString.parse(location.search);
        console.log(id);    
        fetch('/group?id='+id,{
            headers:{
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            }
        }).then(res=>res.json())
        .then(result=>{
            console.log(result.group[0])
            setGrp(result.group[0])
        })
    },[])

    useEffect(()=>{
        if(image)
        {
            const data = new FormData()
            data.append("file",image)
            data.append("upload_preset","chordsNstrings")
            data.append("cloud_name", "chordsnstrings")
            fetch("https://api.cloudinary.com/v1_1/chordsnstrings/image/upload",{
                method:"post",
                body:data
            })
            .then(res=>res.json())
            .then(data=>{
                //setUrl(data.url)
                // localStorage.setItem("user",JSON.stringify({...state, pic:data.url}))
                // //console.log(data.url)
                // dispatch({type:"PICUPDATE", payload:data.url})
                fetch('/updatepic',{
                    method:"put",
                    headers:{
                        "Content-type":"application/json",
                        "Authorization":"Bearer "+localStorage.getItem("jwt")
                    },
                    body:JSON.stringify({
                        pic:data.url
                    })
                }).then(res=>res.json())
                .then(result=>{
                    console.log(result)
                    localStorage.setItem("user",JSON.stringify({...state, pic:data.pic}))
                    dispatch({type:"PICUPDATE", payload:result.pic})
                    // window.location.reload()
                })
               
            })
            .catch(err=>{
                console.log(err)
            })
        }

    },[image])

    const updatePicture = (file)=>{
        setImage(file)
  
    }

    return(
     <div style={{maxWidth:"550px", margin:"0px auto"}}>
         <div style={{
             display:"flex",
             justifyContent:"space-around",
             margin:"18px 0px",
             borderBottom:"1px solid grey"
         }}>
             <div>
                <img style={{width:"160px", height:"160px", borderRadius:"80px"}}
                src={grp?grp.photo:"Loading"} 
                />
             </div>

             {/* <div>
                <h4> {state?state.name:"Loading"} </h4>
                <div style={{display:"flex",
                justifyContent:"space-between",
                width:"110%"
            }}>
                    <h6> {mypics.length} posts </h6> 
                    <h6> {state?state.followers.length:"0"} Followers </h6> 
                    <h6> {state?state.following.length:"0"} Following </h6> 
                </div>
             </div> */}
              <div>
                   <h4>{grp?grp.name:"loading"}</h4>
                   <h6><b>Domain : </b>{grp?grp.domain:"loading"}</h6>
                   <h6><b>Description : </b>{grp?grp.description:"loading"}</h6>
                   <hr></hr>
                   <div style={{display:"flex",justifyContent:"space-between",width:"108%"}}>
                       <h6>{grp.name} posts</h6>
                        {/* <h6>0 Followers </h6>
                       <h6>0 Following</h6>  */}
                       <h6>{grp.members?grp.members.length:"0"} members</h6> 
                        <h6>{grp.pending?grp.pending.length:"0"} following</h6>
                   </div>

               </div>
               
            <div className="file-field input-field">
                <div className="btn #64b5f6 blue darken-1">
                    <span>Change Image</span>
                    <input type="file"
                    onChange={(e)=>updatePicture(e.target.files[0])}/>
                </div>
                {/* <div className="file-path-wrapper">
                    <input className="file-path validate" type="text"/>
                </div> */}
            </div>



         </div>

            <div class="row">
          
            <div class="column">
                <br></br>
            </div>


            <div class="column">
            <button className="btn waves-effect waves-light #64b5f6 blue darken-1"
               onClick={()=> updatePicture()}>
                  Pending Requests
            </button>
         
                &nbsp; &nbsp; &nbsp;  &nbsp; 

                <button className="btn waves-effect waves-light #64b5f6 blue darken-1"
               onClick={()=> createPost()}>
                  Create Post
            </button>


            &nbsp; &nbsp; &nbsp;  &nbsp; 
            
            <button className="btn waves-effect waves-light #64b5f6 blue darken-1"
               onClick={()=> join(grp._id)}>
                  Join Group
            </button>

            </div>

          

          


            </div>

            <div className="home">
           {
               data.map(item=>{
                   return(
                       <div className="card home-card" key={item._id}>
                           {/* <img src={state.pic}/> */}
                           {/* <img src={item.postedBy._id == state._id ? state.pic: item.postedBy.pic}/> */}
                            <h5 style={{padding:"5px"}}><Link to={item.postedBy._id !== state._id?"/profile/"+item.postedBy._id :"/profile"  }>{item.postedBy.name}</Link> 
                            {item.postedBy._id == state._id 
                            && <i className="material-icons" style={{
                                float:"right",
                                color:"red"
                            }} 
                            onClick={()=>deletePost(item._id)}
                            >select</i>

                            }</h5>
                            <div className="card-image">
                                <img src={item.photo}/>
                            </div>
                            <div className="card-content">
                            <i className="material-icons" style={{color:"red"}}>favorite</i>
                            {item.likes.includes(state._id)
                            ? 
                             <i className="material-icons"
                                    onClick={()=>{unlikePost(item._id)}}
                              >thumb_down</i>
                            : 
                            <i className="material-icons"
                            onClick={()=>{likePost(item._id)}}
                            >thumb_up</i>
                            }
                            
                           
                                <h6>{item.likes.length} likes</h6>
                                <hr></hr>
                                <h5><b> {item.title} </b></h5>
                                <p>{item.body}</p>
                                <hr></hr>
                                {
                                    item.comments.map(record=>{
                                        return(
                                        <h6 key={record._id}><span style={{fontWeight:"500"}}>{record.postedBy.name} :</span>{record.text} </h6>
                                        )
                                    })
                                }
                                <form onSubmit={(e)=>{
                                    e.preventDefault()
                                    makeComment(e.target[0].value,item._id)
                                }}>
                                  <input type="text" placeholder="Add a comment" />  
                                </form>
                                
                            </div>
                        </div> 
                   )
               })
           }
          
          
       </div>
     </div>
    )
}

export default GroupProfile