import React,{useState,useEffect,useContext} from 'react'
import {UserContext} from '../../App'
import {Link, useHistory} from 'react-router-dom'

const Group  = ()=>{
    const history = useHistory()

    const [data,setData] = useState([])
    const {state,dispatch} = useContext(UserContext)
    useEffect(()=>{
       fetch('/accredited',{
           headers:{
               "Authorization":"Bearer "+localStorage.getItem("jwt")
           }
       }).then(res=>res.json())
       .then(result=>{
           console.log(result.groups)
           setData(result.groups)
       })
    },[])

    const join = (id)=>{
        fetch('/join',{
            method:"put",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            },
            body:JSON.stringify({
                groupId:id
            })
        }).then(res=>res.json())
        .then(result=>{
            console.log(result)
            //history.push('/group/'+id)
          })
        .catch(err=>{
            console.log(err)
        })
    }

    const leave = (id)=>{
        fetch('/leave',{
            method:"put",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            },
            body:JSON.stringify({
                groupId:id
            })
        }).then(res=>res.json())
        .then(result=>{
            console.log(result)
            //history.push('/group/'+id)
          })
        .catch(err=>{
            console.log(err)
        })
    }
    const viewMore = (id)=>{
        history.push('groupProfile?id='+id);
    }
  const unlikePost = (id)=>{
        fetch('/dislike',{
            method:"put",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            },
            body:JSON.stringify({
                postId:id
            })
        }).then(res=>res.json())
        .then(result=>{
          //   console.log(result)
          const newData = data.map(item=>{
              if(item._id==result._id){
                  return result
              }else{
                  return item
              }
          })
          setData(newData)
        }).catch(err=>{
          console.log(err)
      })
  }

/*
_id: "615802d4827a137e8cd3f598"

country: "India"

createdAt: "2021-10-02T06:57:24.965Z"

curator: "5fc69b9c0e92dd14cd1fd8a9"

description: "Study Group for NEET 2021"

domain: "Medical"

members: Array []

motivation: "To help students facing NEET"

name: "NEET"

pending: Array []

photo: "http://res.cloudinary.com/chordsnstrings/image/upload/v1633157844/yiivykygqabqrjvuaczi.webp"

reasoning: "The group can enable less privileged students with good resources"

states: "Tamilnadu"

status: "unaccredited "
*/
   return (
       <div className="home">
           {
               data.map(item=>{
                   return(
                       <div className="card home-card" key={item._id}>
                           {/* <img src={state.pic}/> */}
                           
                           {/* <img src={item.postedBy._id == state._id ? state.pic: item.postedBy.pic}/> */}
                            <h6 style={{padding:"5px"}}>
                            {true
                            && 
                        <center> <button  class="btn waves-effect waves-light #64b5f6 blue darken-1"
                        onClick={()=>viewMore(item._id)}>
                            View about Group
                        </button> </center>
                           

                            }</h6>
                            <div className="card-image">
                                <img src={item.photo}/>
                            </div>
                            <div className="card-content">
                                
                            <i className="material-icons" style={{color:"red"}}>favorite</i>
                            {item.members.includes(state._id)
                            ? 
                            <>
                            Request to Leave
                             <i className="material-icons"
                                        onClick={()=>{leave(item._id)}}
                              >thumb_down</i>
                            </>
                            : 
                            
                           

                            <button  class="btn waves-effect waves-light #64b5f6 blue darken-1"
                            onClick={()=>{join(item._id)}}>
                                Request to Join
                            </button>

                            }
                            
                           
                                <h6>{item.members.length} members</h6>
                                <hr></hr>
                                <h5><b> {item.name} </b></h5>
                                <p>{item.description}</p>
                               
                                
                                
                            </div>
                        </div> 
                   )
               })
           }
       </div>
   )
}


export default Group