import React, {useState,useEffect} from 'react'
import {Link, useHistory} from 'react-router-dom'
import M from 'materialize-css'
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';

const Blockchain = () =>{
    const history = useHistory()
    const [name, setName] = useState("")
    const [added, setAdded] = useState("")
    const [id, setId] = useState("0")
    const [location, setLocation] = useState("")
    const [status, setStatus] = useState("")
    const [uid,setUid] = useState("");
    const [rating,setRating] = useState(0);
    const [update,setUpdate] = useState("");

    const options = [
        'Learner', 'Curator','Admin'
      ];
    const defaultOption = options[0];

    const [user, setUser] = useState("")
    const [query, setQuery] = useState("")

    
    const UpdateData = ()=>{
        
        if(!uid)
        {
            M.toast({html:"Enter a valid user ID", classes:"#82b1ff blue darken-1"})
            return
        }
     
        setUpdate("Processing")

        fetch("/updaterating",{
            method:"post",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            },
            body: JSON.stringify({
                id : uid,
                rating : rating
            })
        }).then(res=>
            res.json())
        .then(data=>{
           if(data.error)
           {
               M.toast({html: data.error, classes:"#82b1ff blue darken-1"})
               setUpdate("Error in process")
           }
           else{
               M.toast({html: 'Rating updated Successful !', classes: "#00e676 green accent-3"})
               setUpdate(data.message)
               setUid("")
               //history.push('/signin')
           }
        }).catch(err=>{
            console.log(err)
        })
    }
    const CheckData = ()=>{
        if(!user)
        {
            M.toast({html:"Enter a valid user ID", classes:"#82b1ff blue darken-1"})
            return
        }
        setQuery('Processing')
        fetch("/checkrating",{
            method:"post",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            },
            body: JSON.stringify({
                id : user
            })
        }).then(res=>
            res.json())
        .then(data=>{
            console.log(data)
           if(data.error)
           {
               M.toast({html: data.error, classes:"#82b1ff blue darken-1"})
               setQuery('Error in Query')
           }
           else{
               
               M.toast({html:"Query Successful !", classes: "#00e676 green accent-3"})
               setQuery(data.message+"Token Rating")
           }
        }).catch(err=>{
            console.log(err)
        })
    }


      

    const PostData = ()=>{
        
        if(!name)
        {
            M.toast({html:"Enter a valid name", classes:"#82b1ff blue darken-1"})
            return
        }
     
        setAdded("Processing")

        fetch("/adduser",{
            method:"post",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem("jwt")
            },
            body: JSON.stringify({
                id,
                name,
                location,
                status
            })
        }).then(res=>
            res.json())
        .then(data=>{
           if(data.error)
           {
               M.toast({html: data.error, classes:"#82b1ff blue darken-1"})
               setAdded("Error in process")
           }
           else{
               M.toast({html: 'Added Successfully !', classes: "#00e676 green accent-3"})
               setAdded(data.message)
               console.log(data)
               setId(data.id.toString())
               setName("")
               //history.push('/signin')
           }
        }).catch(err=>{
            console.log(err)
        })
    }

    return(
        <>
        <div className="mycard">
       <center>
            <h4> Ethereum Blockchain based Learner Rating system </h4> </center>
             <div className="card auth-card input-field #e8eaf6 indigo lighten-5">
                <h4>Please Fill User Details</h4>
                <input type="text" 
                placeholder="Name" 
                value={name} 
                onChange={(e)=>setName(e.target.value)}/>

                <input type="text" 
                placeholder="User ID" 
                value={id} 
                onChange={(e)=>setId(e.target.value)}/>   

                <input type="text" 
                placeholder="location" 
                value={location} 
                onChange={(e)=>setLocation(e.target.value)}/>

                <Dropdown 
                options={options} 
                onChange={(e)=> setStatus(e.value)}
                value={defaultOption} 
                placeholder="Select Status" />
                <br/>

                

                <button className="btn waves-effect waves-light #64b5f6 blue darken-1" onClick={()=>PostData()}>
                    Register User
                </button>
                
            </div>
            

            
        </div>

        <br>
        </br>
        <br>
        </br>
        <div className="mycard">
             <div className="card auth-card input-field  #e8eaf6 indigo lighten-5">
                <h4>Check User Rating</h4>
                <input type="text" 
                placeholder="Enter User ID"
                value={user}
                onChange= {(e)=> setUser(e.target.value)}/>

              
                <button className="btn waves-effect waves-light #64b5f6 blue darken-1"
                onClick={()=> CheckData()}>
                    Check
                </button>

                    &nbsp;                

                
            </div>
            <div className="card auth-card input-field  #e8eaf6 indigo lighten-5">
            {query}
            </div>
        </div>

        <br></br>
        <br></br>


        <div className="mycard">
             <div className="card auth-card input-field #e8eaf6 indigo lighten-5">
                <h4>Update User Rating</h4>
                <input type="text" 
                placeholder="Enter User ID"
                value={uid}
                onChange= {(e)=> setUid(e.target.value)}/>

                <input type="number" 
                placeholder="Enter new rating"
                value={rating}
                onChange= {(e)=> setRating(e.target.value)}/>

            

                

               
                <button className="btn waves-effect waves-light #64b5f6 blue darken-1" onClick={()=>UpdateData()}>
                    Update Rating
                </button>
                
            </div>
            <div className="card auth-card input-field #e8eaf6 indigo lighten-5">
                {update}
            </div>
        </div>

        </>
    )
}

export default Blockchain